* {
	box-sizing: border-box;
	&:before {
		box-sizing: border-box;
	}
	&:after {
		box-sizing: border-box;
	}
}

.page__body {
	font-family: var(--main-font);
	font-size: 16px;
	height: 100%;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	font-size: 15px;
	line-height: 133%;
	color: var(--black);
	&--open {
		overflow: hidden;
	}
}

.page-footer {
	margin-top: auto;
}

img {
	max-width: 100%;
	height: auto;
}

.img-fluid {
	max-width: 100%;
	height: auto;
}

img, svg, iframe {
	vertical-align: top;
}

::placeholder {
	opacity: 1;
}

::selection {
	background-color: var(--black);
	color: #fff;
}


@media(min-width: 375px) and (max-width: 767px) {
	.row {
		--bs-gutter-x: 80px;
	}
	.container, .container-fluid, .container-lg, .container-md, .container-sm, .container-xl, .container-xxl {
		padding-right: var(--bs-gutter-x,40px);
		padding-left: var(--bs-gutter-x,40px);
	}
}
